.star {
  display: flex;
}

.star__star {
  margin-right: 4px;
  height: 16px;
  width: 16px;
  background-image: url(../images/star.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.stars--1 .star__star:nth-child(-n + 1) {
  background-image: url(../images/star-active.svg);
}

.stars--2 .star__star:nth-child(-n + 2) {
  background-image: url(../images/star-active.svg);
}

.stars--3 .star__star:nth-child(-n + 3) {
  background-image: url(../images/star-active.svg);
}

.stars--4 .star__star:nth-child(-n + 4) {
  background-image: url(../images/star-active.svg);
}

.stars--5 .star__star:nth-child(-n + 5) {
  background-image: url(../images/star-active.svg);
}
